#containerAscii {
    position: relative;
    height: 80%;
  }
  
  #asciiWrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
  }
  
  #Ascii {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    font-size: 8px;
  }
  
  #Ascii span {
    display: block;
    opacity: 0;
    animation: typing 5s steps(3000, end) forwards infinite;
    animation-delay: 60s;
  }
  
  #Ascii span:nth-child(3n) {
    animation-delay: 1.5s;
  }
  
  #Ascii span:nth-child(5n) {
    animation-delay: 2s;
  }
  
  @keyframes typing {
    from {
      opacity: 0;
      transform: translateX(-50%);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  